<template>
  <div class="selectScreenMode">
    <vx-header :showback="1" :title="$t('selectScreenMode.quickScreen')"></vx-header>
    <div class="vx-container">
      <div class="layoutRight">
        <div class="screenTitle">
          <!-- 展示箱体大小，及屏体大小 -->
          <div class="numBox">
            <div v-for="(item, index) in showScreenSize" :key="index">
              <span>{{item.title}}</span>
              <span>{{item.data}}</span>
            </div>
          </div>
          <div class="viewPort">
            <div class="rowcolBox" ref="rowcolBox">
              <table class="table" ref="table" :class="{reversalsY: colorNet.reversalY, reversalsX: colorNet.reversalX}">
                <tr v-for="(item, index) in getQuickPointScreen['boxY']" :key="index" ref="col" :class="{['a'+parseInt(index / colorNet.netBoxNum)]: colorNet.walktypeRow}">
                  <td v-for="(item,  index) in getQuickPointScreen['boxX']" :key="index" ref="row" :class="{['a'+parseInt(index / colorNet.netBoxNum)]: colorNet.walktypeCol}" :style="{width:boxParamSize.boxWidth, height: boxParamSize.boxHeight}"></td>
                </tr>
              </table>
            </div>
            <div class="tip">
              <span v-show="tip.netOne">{{$t('selectScreenMode.networkPort')}} <span v-for="(item, index) in nowNetNum">{{dun(index, nowNetNum)}}</span> {{$t('selectScreenMode.netOne')}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="layoutLeft">
        <div class="rowColNetList">
          <vx-progress :title="$t('selectScreenMode.rows')" :num="getQuickPointScreen['boxY']" :range="ranges.rows" :step="1" @update="updateY" @touchend="save"></vx-progress>
          <vx-progress :title="$t('selectScreenMode.columns')" :num="getQuickPointScreen['boxX']" :range="ranges.columns" :step="1" @update="updateX" @touchend="save"></vx-progress>
          <vx-progress :title="$t('selectScreenMode.CarryingCapacityNetworkPortOne')" :num="getQuickPointScreen['loadNum']" :range="ranges.net" :step="1" @update="updateNet" @touchend="save"></vx-progress>
        </div>
        <div class="walkType">
          <div class="walkType-title">
            {{$t('selectScreenMode.selectrouteMethod')}}
          </div>
          <div class="walkType-list">
            <div v-for="(item, index) in imgList" :key="index">
              <div class="img-list" :class="item.walkType===selectWalkType?item.selectImg:item.normalImg" @click="selectWalkTypes(item)"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import htp from "../../api/http";
  import url from '../../api/apiurl';
  import { setLoc, getLoc, setSen, getSen, clearLocalKey } from '../../utils';
  // 行数* 箱体高度 < 2160
  // 列数* 箱体宽度 < 4096
  // （行数*箱体高度+62）*（列数*箱体宽度+160）* 60 < (39583333*6)
  // 网口1带宽限制：带载数量*箱体宽度*箱体高度*60<39583333
  // 网口1带载：
  // (箱体行数*箱体列数/设备输出网口数量[6])(向上取整)<带载数量<(箱体行数*箱体列数)
  export default {
    data() {
      return {
        nowNetNum: null,
        tip: {
          netOne: false,
          netsub: ''
        },
        scrollStyle: {},
        colorNet: {
          netBoxNum: null,
          walktypeRow: null,
          walktypeCol: null,
          reversalY: null,
          reversalX: null
        },
        calculation: {
          resolution: {},
          portNumber: null,
          boxParam: {}
        },
        showScreenSize: [],
        getQuickPointScreen: {},
        boxParamSize: {
          boxWidth: null,
          boxHeight: null
        },
        rowcolBoxSize: {
          width: null,
          height: null
        },
        rowColBoxDom: {
          rowColBox: null,
          styles: null,
          table: null,
          tableStyle: null
        },
        reversals: {
          reversalsX: false,
          reversalsY: false,
          walktypeRow: false,
          walktypeCol: false
        },
        ranges: {
          rows: {
            start: 1,
            end: null
          },
          columns: {
            start: 1,
            end: null
          },
          net: {
            start: 1,
            end: null
          }
        },
        selectWalkType: '',
        imgList: [
          {
            normalImg: 'normal_img1',
            selectImg: 'select_img1',
            walkType: 0
          },
          {
            normalImg: 'normal_img2',
            selectImg: 'select_img2',
            walkType: 1
          },
          {
            normalImg: 'normal_img3',
            selectImg: 'select_img3',
            walkType: 2
          },
          {
            normalImg: 'normal_img4',
            selectImg: 'select_img4',
            walkType: 3
          },
          {
            normalImg: 'normal_img5',
            selectImg: 'select_img5',
            walkType: 4
          },
          {
            normalImg: 'normal_img6',
            selectImg: 'select_img6',
            walkType: 5
          },
          {
            normalImg: 'normal_img7',
            selectImg: 'select_img7',
            walkType: 6
          },
          {
            normalImg: 'normal_img8',
            selectImg: 'select_img8',
            walkType: 7
          }
        ]
      };
    },
    created() {
      console.log(11111);
    },
    mounted() {
      this.initData();
      window.addEventListener("orientationchange", () => {
        // 根据event.orientation|screen.orientation.angle等于0|180、90|-90度来判断横竖屏
        if(this.$router.history.current.name === 'quickscreen') {
          setTimeout(() => {
            this.initBoxParamSize();
          }, 100);
        }

      }, false);
    },
    methods: {
      dun(index, item) {
        let str = (index + 1) + `、`;
        if(index === item - 1) {
          str = (index + 1);
        }
        return str;
      },
      initData() {
        this.calculation.resolution = getLoc('resolution');
        this.calculation.portNumber = getLoc('portNumber');
        this.calculation.boxParam = getLoc('boxParam');

        this.getQuickScreenData().then(res => {
          if(res.status === 10000000) {
            this.getQuickPointScreen = res.data;
            this.selectWalkType = this.getQuickPointScreen['walkType'];
            this.getSwitch(this.selectWalkType, this.getQuickPointScreen['loadNum']);
            this.getNumBoxData(res.data.boxX, res.data.boxY);
            this.initBoxParamSize();
            if(this.calculation.boxParam) {
              //配置头部数据
              this.ranges = {
                rows: {
                  start: 1,
                  end: Math.floor(2160 / this.calculation.boxParam.height)
                },
                columns: {
                  start: 1,
                  end: Math.floor(4096 / this.calculation.boxParam.width)
                },
                net: {
                  start: 1,
                  end: 40
                }
              };
            }
          }
        });
      },
      // 获取快捷点屏数据
      getQuickScreenData: async function() {
        return htp.get(url.getQuickPointScreen);
      },
      getNumBoxData(x, y) {
        if(this.calculation.boxParam) {
          //配置头部数据
          this.showScreenSize = [
            {
              title: this.$t('selectScreenMode.resolutionSize'),
              data: `${this.calculation.boxParam.width}x${this.calculation.boxParam.height}`
            },
            {
              title: this.$t('scenarist.showSize'),
              data: `${parseInt(this.calculation.boxParam.width) * x}x${parseInt(this.calculation.boxParam.height * y)}`
            }
          ];
        }
      },
      initBoxParamSize() {
        this.rowColBoxDom.styles = this.getStyle(this.$refs['rowcolBox']);
        this.rowColBoxDom.tableStyle = this.getStyle(this.$refs['table']);
        this.rowcolBoxSize.width = this.rowColBoxDom.styles.width;
        this.rowcolBoxSize.height = this.rowColBoxDom.styles.height;
        this.boxParamSize.boxWidth = Math.round(this.calculation.boxParam.width * parseFloat(this.rowcolBoxSize.width) / 4096) / 75 * 2 + 'rem';
        this.boxParamSize.boxHeight = Math.round(this.calculation.boxParam.height * parseFloat(this.rowcolBoxSize.height) / 2048) / 75 * 2 + 'rem';
      },
      getStyle(element) {
        return window.getComputedStyle(element, null);
      },
      //行
      updateY(val) {
        this.getQuickPointScreen['boxY'] = val;
        this.getColMix(val);
        this.getNetMix(val, this.getQuickPointScreen['boxX']);
        this.getSwitch(this.selectWalkType, this.getQuickPointScreen['loadNum']);
        this.getNumBoxData(this.getQuickPointScreen['boxX'], val);
      },
      //列
      updateX(val) {
        this.getQuickPointScreen['boxX'] = val;
        this.getRowMix(val);
        this.getNetMix(this.getQuickPointScreen['boxY'], val);
        this.getSwitch(this.selectWalkType, this.getQuickPointScreen['loadNum']);
        this.getNumBoxData(val, this.getQuickPointScreen['boxY']);
      },
      updateNet(val) {
        this.getQuickPointScreen['loadNum'] = val;
        this.getSwitch(this.selectWalkType, this.getQuickPointScreen['loadNum']);
      },
      getRowMix(col) {
        let rowEnd;
        rowEnd = 2160 / this.calculation.boxParam.height;
        rowEnd = Math.floor((237499998 / 60 / (col * this.calculation.boxParam.width + 160) - 62) / this.calculation.boxParam.height);
        if(this.getQuickPointScreen['boxY'] > rowEnd) {
          this.getQuickPointScreen['boxY'] = rowEnd;
        }
        if(col <= 13) {
          rowEnd = Math.floor(2160 / this.calculation.boxParam.height);
        }
        this.getSwitch(this.selectWalkType, this.getQuickPointScreen['loadNum']);
        // this.ranges.rows.end = rowEnd;
      },
      getColMix(row) {
        let colEnd;
        colEnd = 4096 / this.calculation.boxParam.width;
        colEnd = Math.floor((237499998 / 60 / (row * this.calculation.boxParam.height + 62) - 160) / this.calculation.boxParam.width);
        if(this.getQuickPointScreen['boxX'] > colEnd) {
          this.getQuickPointScreen['boxX'] = colEnd;
        }
        if(row <= 6) {
          colEnd = Math.floor(4096 / this.calculation.boxParam.width);
        }
        // this.ranges.columns.end = colEnd;
      },
      getNetMix(row, col) {
        let netEnd = Math.floor(39583333 / 60 / this.calculation.boxParam.width / this.calculation.boxParam.height);
        netEnd = row * col > netEnd ? netEnd : row * col;
        let startEnd;
        startEnd = Math.ceil(row * col / this.calculation.portNumber) < startEnd ? startEnd : Math.ceil(row * col / this.calculation.portNumber);
        this.ranges.net.end = netEnd;
        this.ranges.net.start = startEnd;
        if(this.getQuickPointScreen['loadNum'] > netEnd) {
          this.getQuickPointScreen['loadNum'] = netEnd;
        }
        if(this.getQuickPointScreen['loadNum'] < startEnd) {
          this.getQuickPointScreen['loadNum'] = startEnd;
        }
      },
      selectWalkTypes(item) {
        this.selectWalkType = item.walkType;
        this.getSwitch(this.selectWalkType, this.getQuickPointScreen['loadNum']);
        this.save();
      },
      // 行数*列数*箱体宽度*箱体高度*60<39583333
      getSwitch(val, num) {
        switch(val) {
          case 0:
          case 2:
            this.colorNet.walktypeRow = true;
            this.colorNet.walktypeCol = false;
            this.colorNet.reversalY = false;
            this.colorNet.netBoxNum = Math.ceil(num / this.getQuickPointScreen['boxX']);
            let nowNetNum = Math.ceil(parseInt(this.getQuickPointScreen['boxY']) / this.colorNet.netBoxNum);
            let curNetNum = (parseInt(this.getQuickPointScreen['boxY']) % this.colorNet.netBoxNum) === 0;
            let isBeyond = this.colorNet.netBoxNum * parseInt(this.getQuickPointScreen['boxX']) * parseInt(this.calculation.boxParam.height) * parseInt(this.calculation.boxParam.width) * 60 > 39583333;
            if(isBeyond) {
              console.log('网口1超了');
              this.tip.netOne = true;
              this.nowNetNum = nowNetNum - 1;
              if(curNetNum) {
                console.log('全超了');
                this.nowNetNum = nowNetNum;
              }
            } else {
              this.tip.netOne = false;
            }
            break;
          case 1:
          case 5:
            this.colorNet.walktypeCol = true;
            this.colorNet.walktypeRow = false;
            this.colorNet.reversalX = false;
            this.colorNet.netBoxNum = Math.ceil(num / this.getQuickPointScreen['boxY']);
            nowNetNum = Math.ceil(parseInt(this.getQuickPointScreen['boxX']) / this.colorNet.netBoxNum);
            curNetNum = (parseInt(this.getQuickPointScreen['boxX']) % this.colorNet.netBoxNum) === 0;
            console.log(parseInt(this.getQuickPointScreen['boxX']) % this.colorNet.netBoxNum);
            isBeyond = this.colorNet.netBoxNum * parseInt(this.getQuickPointScreen['boxY']) * parseInt(this.calculation.boxParam.height) * parseInt(this.calculation.boxParam.width) * 60 > 39583333;
            if(isBeyond) {
              console.log('网口1超了');
              this.tip.netOne = true;
              this.nowNetNum = nowNetNum - 1;
              if(curNetNum) {
                console.log('全超了');
                this.nowNetNum = nowNetNum;
              }
            } else {
              this.tip.netOne = false;
            }
            break;
          case 3:
          case 7:
            this.colorNet.walktypeCol = true;
            this.colorNet.walktypeRow = false;
            this.colorNet.reversalX = true;
            this.colorNet.netBoxNum = Math.ceil(num / this.getQuickPointScreen['boxY']);
            nowNetNum = Math.ceil(parseInt(this.getQuickPointScreen['boxX']) / this.colorNet.netBoxNum);
            curNetNum = (parseInt(this.getQuickPointScreen['boxX']) % this.colorNet.netBoxNum) === 0;
            console.log(parseInt(this.getQuickPointScreen['boxX']) % this.colorNet.netBoxNum);
            isBeyond = this.colorNet.netBoxNum * parseInt(this.getQuickPointScreen['boxY']) * parseInt(this.calculation.boxParam.height) * parseInt(this.calculation.boxParam.width) * 60 > 39583333;
            if(isBeyond) {
              console.log('网口1超了');
              this.tip.netOne = true;
              this.nowNetNum = nowNetNum - 1;
              if(curNetNum) {
                console.log('全超了');
                this.nowNetNum = nowNetNum;
              }
            } else {
              this.tip.netOne = false;
            }
            break;
          case 4:
          case 6:
            this.colorNet.walktypeRow = true;
            this.colorNet.walktypeCol = false;
            this.colorNet.reversalY = true;
            this.colorNet.netBoxNum = Math.ceil(num / this.getQuickPointScreen['boxX']);
            nowNetNum = Math.ceil(parseInt(this.getQuickPointScreen['boxY']) / this.colorNet.netBoxNum);
            curNetNum = (parseInt(this.getQuickPointScreen['boxY']) % this.colorNet.netBoxNum) === 0;
            isBeyond = this.colorNet.netBoxNum * parseInt(this.getQuickPointScreen['boxX']) * parseInt(this.calculation.boxParam.height) * parseInt(this.calculation.boxParam.width) * 60 > 39583333;
            if(isBeyond) {
              console.log('网口1超了');
              this.tip.netOne = true;
              this.nowNetNum = nowNetNum - 1;
              if(curNetNum) {
                console.log('全超了');
                this.nowNetNum = nowNetNum;
              }
            } else {
              this.tip.netOne = false;
            }
            break;
          default:
            break;
        }
      },
      save() {
        let params = {};
        params.boxY = this.getQuickPointScreen['boxY'];
        params.boxX = this.getQuickPointScreen['boxX'];
        params.loadNum = this.getQuickPointScreen['loadNum'];
        params.walkType = this.selectWalkType;
        console.log(params);
        htp.post(url.setQuickPointScreen, params)
          .then(res => {
            if(res.status === 10000000) {
              clearLocalKey('selport');
            }
          });
      }
    }
  };
</script>
<style scoped lang="less">
  .vx-container {
    -webkit-overflow-scrolling: auto !important;
  }
  .tip {
    width: 100%;
    margin-top: 5px;
    text-align: left;
    text-indent: 80px;
    span {
      font-size: 12px; /*no*/
      color: red;
    }
  }
  .numBox {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    color: #fff;
    margin: 20px 0 0 0;

    > div {
      margin-left: 40px;
      height: 100%;
      font-size: 26px;
      > span:nth-child(2) {
        margin-left: 25px;
        color: #ddd;
      }
    }
  }
  .viewPort {
    width: 100%;
    height: 385px;
    padding-top: 20px;
    .rowcolBox {
      width: 78%;
      height: 88%;
      background: #2c2c2f;
      border: 1px solid #808080; /*no*/
      margin: auto;
      overflow: hidden;
      .table {
        width: auto;
        border-spacing: 0;
        border-left: 1px solid #fff; /*no*/
        border-top: 1px solid #fff; /*no*/
        > tr {
          > td {
            border-right: 1px solid #fff; /*no*/
            border-bottom: 1px solid #fff; /*no*/
            padding: 0;
          }
        }
      }
    }
  }
  .a0 {
    background: #e84545;
  }
  .a1 {
    background: #d44ecf;
  }
  .a2 {
    background: #7a3be2;
  }
  .a3 {
    background: #4899fc;
  }
  .a4 {
    background: #51ca56;
  }
  .a5 {
    background: #34cbcb;
  }

  .reversalsY {
    transform: scaleY(-1);
  }
  .reversalsX {
    transform: scaleX(-1);
  }
  .rowColNetList {
    line-height: 60px;
    text-align: left;
    text-indent: 35px;
    background: #383940;
    font-size: 24px;
    .vx-progress {
      color: #a8a8aa;
      padding-top: 30px !important;
      padding-bottom: 30px !important;
    }
  }
  .walkType {
    width: 100%;
    .walkType-title {
      color: #aeaeae;
      padding: 20px 0 20px 35px;
      text-align: left;
      font-size: 14px; /*no*/
    }
    .walkType-list {
      width: 100%;
      height: 255px;
      background: #383940;
      padding: 20px 10px 10px 10px;
      display: flex;
      flex-wrap: wrap;
      > div {
        width: 25%;
        height: 50%;
        display: flex;
        justify-content: center;
        .img-list {
          width: 100px;
          height: 100px;
          border-radius: 16px;
        }
      }
    }
  }

  .normal_img1 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_1.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img1 {
    // background: url('../../assets/selectRouMethod/v1-pressed.png') center center / cover;
    background: #282828 url('../../assets/selectRouMethod/btn_1.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  .normal_img2 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_6.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img2 {
    background: #282828 url('../../assets/selectRouMethod/btn_6.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  .normal_img3 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_5.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img3 {
    background: #282828 url('../../assets/selectRouMethod/btn_5.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  .normal_img4 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_2.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img4 {
    background: #282828 url('../../assets/selectRouMethod/btn_2.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  .normal_img5 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_7.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img5 {
    background: #282828 url('../../assets/selectRouMethod/btn_7.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  .normal_img6 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_4.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img6 {
    background: #282828 url('../../assets/selectRouMethod/btn_4.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  .normal_img7 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_3.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img7 {
    background: #282828 url('../../assets/selectRouMethod/btn_3.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  .normal_img8 {
    border: 2px solid #888b91;
    background: url('../../assets/selectRouMethod/btn_8.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img8 {
    background: #282828 url('../../assets/selectRouMethod/btn_8.png') center center no-repeat;
    border: 2px solid #e67007;
    background-size: 65%;
  }
  @media screen and (orientation: landscape) {
    .tip {
      text-indent: 30px;
    }
    .vx-container {
      display: flex;
    }
    .layoutRight {
      padding-top: 50px;
      flex: 1;
      .numBox {
        display: flex;
        justify-content: space-between;
        padding: 0 30px;
        > div {
          margin: 0;
          > span {
            margin-left: 0;
          }
        }
      }
    }
    .viewPort {
      .rowcolBox {
        width: 88%;
        height: 88%;
      }
    }
    .layoutLeft {
      flex: 1;
      height: 100%;
      overflow-y: auto;
    }
  }
</style>





